.footer {
  height: 70px;
  width: 100%;
  align-items: center;
  display: flex;
  justify-content: space-evenly;
  flex: 0 0 auto;
}
.footer__year {
  font-family: cursive;
  font-size: 26px;
  padding-right: 20px;
  color: bisque;
}
.footer__logo {
  background-image: url(../../assets/svg/rss.svg);
  height: 45px;
  width: 80px;
  cursor: pointer;
  background-repeat: no-repeat;
  display: flex;
  background-position: center;
}
.footer__logo:hover {
  background-color: black;
  border-radius: 10px;
  border: 2px solid white;
  box-shadow: 5px 5px 5px black;
}
.creators-wrapper {
  display: flex;
  column-gap: 35px;
  height: 50px;
  align-items: center;
}
.creator-name {
  text-decoration: none;
  font-size: 21px;
  color: beige;
  font-family: cursive;
}
.creator-name:hover {
  color: aliceblue;
}
.creator-name:hover:before {
  content: '';
  background: url('../../assets/png/github.png') no-repeat center center;
  height: 20px;
  width: 20px;
  background-size: contain;
  position: absolute;
  margin-left: -24px;
  margin-top: 5px;
}

@media (max-width: 900px) {
  .creator-name {
    font-size: 15px;
  }

  .footer__year {
    font-size: 17px;
  }
}
@media (max-width: 450px) {
  .creator-name {
    font-size: 8px;
  }
  .footer__year {
    font-size: 9px;
  }
  .footer__logo {
    width: 40px;
    height: 25px;
    background-size: contain;
    margin-left: 10px;
    padding-right: 10px;
  }
  .creators-wrapper {
    column-gap: 10px;
  }
}
